body {
  background-color: #F6F6F6;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a
{
  text-decoration: none;
  color: white;
}

#container
{
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  opacity: 1;
  background-color: rgb(253, 253, 253);
}

.vehicles
{
  height: 400px;
  overflow-y: auto;
  margin-bottom: 3rem 0px;
  width: 655px;
}

.vehicle
{
  padding: 0.5em;
  margin: 0px 5px;
  width: 294px;
  margin-bottom: 10px;
  background-color: #5A59F3;
  color: white;
  text-decoration: none;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  display: inline-block;
}

#title
{
  text-align: center;
  color: #524EEE;
  margin-bottom: 2rem;
  font-size: 3rem;
}

.shadow
{
  -webkit-box-shadow: 0px 20px 38px -37px rgba(0,0,0,1);
  -moz-box-shadow: 0px 20px 38px -37px rgba(0,0,0,1);
  box-shadow: 0px 20px 38px -37px rgba(0,0,0,1); 
}

#view
{
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column nowrap;
}

.button
{
  border-radius: 100%;
  background-color: #5A59F3;
  width: 50px;
  height: 50px;
  display: block;
  font-weight: bold;
  line-height: 43px;
  text-align: center;
  font-size: 2rem;
}

form
{
  margin: 2rem 0px;
}

form input
{
  padding: 1rem;
  background-color: #5A59F3;
  color: white;
  outline: none;
  border-color: transparent;
}

form input:focus
{
  outline: none;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
}

form input::placeholder
{
  color: white;
}

form input[type="submit"]
{
  border-left: 1px solid rgb(207, 207, 207);
  cursor: pointer;
}

label
{
  font-weight: bold;
  font-size: 1rem;
}

.error, .red
{
  color: #F8312C;
}

.delete
{
  color: white;
  font-weight: bold;
  cursor: pointer;
  background-color: #F8312C;
  padding: .5rem;
  text-align: center;
  border-radius: 10px;
}

.vehicle-info
{
  margin: 2rem 0px;
  font-size: 1.5rem;
  background-color: #5A59F3;
  color: white;
  padding: 1rem 2rem;
  border-radius: 5px;
}